uct-block {\n  a {\n    flex-direction: column;\n    flex-wrap: nowrap;\n  }\n\n\n\n  h3 {\n    margin-top: -.2em;\n  }\n\n  .svg-ess-cube {\n    height: 25%;\n    margin-bottom: 0;\n  }\n\n  &.main-product-block {\n    h3 {\n      font-weight: bold;\n    }\n\n\n    a:hover {\n      background: color('gray', 'lighten-4');\n    }\n  }\n\n  .main-product-logo {\n    height: 50%;\n    margin-bottom: 0;\n  }\n}\n\n\n#cube-ess-logo {\n  width: 40%;\n  max-width: 250px;\n}\n\n\n@media only screen and (min-width: $medium-screen) {}","$ess-blue: #00C0F3;\r\n$ess-blue-dark: #0f2a56;\r\n$ess-blue-supplementary: #174f72;\r\n$ess-sand: #e1e0d4;\r\n$dynairix-cyan: #00f0f0;\r\n$dynairix-cyan-darker: #0ac4f3;\r\n\r\n$primary-color-transp: rgba(60, 180, 255, 0.5);\r\n$secondary-color-transp: rgba(15, 42, 86, 0.5);\r\n$ess-darkest-transp: rgba(12, 22, 46, 0.5);\r\n$ess-darkest-full: rgba(12, 22, 46, 1);\r\n\r\n// background of the navigation cube on pages \r\n$light-gray: rgb(240, 240, 240);\r\n$nav-cube-background: radial-gradient(circle, rgba(230, 230, 230, 1) 0%, rgba(211, 211, 211, 0.85) 77%);\r\n\r\n$essblue: (\r\n  \"base\" : #14bef0,\r\n  \"lighten-1\" : #92e5ff,\r\n  \"lighten-2\" : #d2f5fa,\r\n  \"lighten-3\" : #f0faff,\r\n  \"darken-1\" : #14a0ff,\r\n  \"darken-2\" : #2d78c8,\r\n  \"darken-3\" : #0c3c92,\r\n  \"darken-4\" : #0f2a57\r\n);\r\n\r\n$green: (\r\n  \"base\" : #86b755,\r\n  \"lighten-2\" : #81C784,\r\n  \"lighten-1\" : #66BB6A,\r\n  \"darken-1\" : #43A047,\r\n  \"darken-2\" : #388E3C\r\n);\r\n\r\n\r\n$red: (\r\n  \"base\" : #F44336,\r\n  \"lighten-2\" : #E57373,\r\n  \"lighten-1\" : #EF5350,\r\n  \"darken-1\" : #E53935,\r\n  \"darken-2\" : #D32F2F\r\n);\r\n\r\n\r\n$gray: (\r\n  \"base\" : #BEBEBE,\r\n  \"darken-1\" : #787878,\r\n  \"darken-2\" : #4D4D4D,\r\n  \"darken-3\" : #2D2D2D,\r\n  \"lighten-1\" : #D4D4D4,\r\n  \"lighten-2\" : #E6E6E6,\r\n  \"lighten-3\" : #E1E1E1,\r\n  \"lighten-4\" : #F5F5F5,\r\n  \"lighten-5\" : #F9F9F9,\r\n\r\n);\r\n\r\n$processing: (\r\n  \"base\" : #d4cba4,\r\n  \"darken-1\" : #c2b98e,\r\n  \"darken-2\" : #9d906f,\r\n  \"lighten-1\": #e5debf,\r\n  \"lighten-2\": #f3efde,\r\n);\r\n\r\n$paintshop: (\r\n  \"base\" : #9b64aa,\r\n  \"darken-1\" : #895aa4,\r\n  \"darken-2\" : #5b266c,\r\n  \"lighten-1\": #bf8dcd,\r\n  \"lighten-2\": #f1e4f5,\r\n\r\n);\r\n\r\n$data-cleaning: (\r\n  \"base\" : #0ea285,\r\n  \"darken-1\" : #108673,\r\n  \"darken-2\" : #006654,\r\n  \"lighten-1\": #86d2c3,\r\n  \"lighten-2\": #ddf6f1\r\n);\r\n\r\n$mobility: (\r\n  \"base\" : #eeaa32,\r\n  \"darken-1\" : #be8b37,\r\n  \"darken-2\" : #ae7d36,\r\n  \"lighten-1\": #eec886,\r\n  \"lighten-2\": #f7ebd6\r\n);\r\n\r\n\r\n$washing: (\r\n  \"base\" : #9ad9eb,\r\n  \"darken-1\" : #68b2c6,\r\n  \"darken-2\" : #43888d,\r\n  \"lighten-1\": #c1e8f3,\r\n  \"lighten-2\": #e8f7fa,\r\n);\r\n$environment: (\r\n  \"base\" : #a4d06e,\r\n  \"darken-1\" : #78a94e,\r\n  \"darken-2\" : #577d40,\r\n  \"lighten-1\": #c7e5a2,\r\n  \"lighten-2\": #ecf9dd,\r\n);\r\n$oil-gas: (\r\n  \"base\" : #52656e,\r\n  \"darken-1\" : #294146,\r\n  \"darken-2\" : #001d27,\r\n  \"lighten-1\": #7995a2,\r\n  \"lighten-2\": #c5d6de,\r\n);\r\n\r\n$shades: (\r\n  \"black\" : #000000,\r\n  \"white\" : #FFFFFF,\r\n  \"transparent\" : transparent\r\n);\r\n\r\n$colors-ess: (\r\n  \"essblue\": $essblue,\r\n  \"red\": $red,\r\n  \"green\": $green,\r\n  \"gray\": $gray,\r\n  \"shades\": $shades,\r\n  \"processing\":$processing,\r\n  \"data-cleaning\": $data-cleaning,\r\n  \"paintshop\": $paintshop,\r\n  \"mobility\": $mobility,\r\n  \"washing\": $washing,\r\n  \"environment\": $environment,\r\n  \"oil-gas\": $oil-gas\r\n);\r\n\r\n@function color($color, $type) {\r\n  @if map-has-key($colors-ess, $color) {\r\n    $curr_color: map-get($colors-ess, $color);\r\n\r\n    @if map-has-key($curr_color, $type) {\r\n      @return map-get($curr_color, $type);\r\n    }\r\n  }\r\n\r\n  @warn \"Unknown `#{$color}` - `#{$type}` in $colors.\";\r\n  @return null;\r\n}\r\n\r\n\r\n// MIXINS FOR GRADIENT AND OPACITY -------------------------------\r\n\r\n\r\n@mixin opacity($value) {\r\n  opacity: $value;\r\n}\r\n\r\n@mixin radial-gradient($outer_color, $inner_color) {\r\n  background: radial-gradient($outer_color, $inner_color);\r\n}","/* \r\nTABLE OF CONTENTS\r\n\r\n  1. Sizes\r\n  2. Typography\r\n  3. Colors\r\n  4. Spacings\r\n  5. Top Navbar\r\n\r\n*/\r\n\r\n\r\n// 1. Sizes\r\n// ==========================================================================\r\n\r\n$small-screen-up : 461px;\r\n$medium-screen-up : 761px;\r\n$large-screen-up : 1201px;\r\n\r\n$small-screen : 460px;\r\n$medium-screen : 760px;\r\n$large-screen : 1200px;\r\n\r\n$base-margin : calc(0.8rem + 0.5vw);\r\n$double-margin : calc(1.6rem + 1vw);\r\n$quad-margin : calc(3.2rem + 2vw);\r\n$sexta-margin : calc(4.8rem + 3vw);\r\n$okto-margin : calc(6.4rem + 4vw);\r\n$dodeca-margin : calc(9.6rem + 6vw);\r\n\r\n$half-margin : calc(0.4rem + 0.25vw);\r\n$quarter-margin : calc(0.2rem + 0.15vw);\r\n$reduced-margin : 1rem;\r\n\r\n\r\n$-quarter-margin : calc((0.2rem + 0.15vw) * -1);\r\n$-base-margin : calc((0.8rem + 0.5vw) * -1);\r\n$-double-margin : calc((0.8rem + 0.5vw) * -2);\r\n$-quad-margin : calc((0.8rem + 0.5vw) * -4);\r\n$-sexta-margin : calc((0.8rem + 0.5vw) * -6);\r\n$-okto-margin : calc((0.8rem + 0.5vw) * -8);\r\n\r\n\r\n// 2. Typography\r\n// ========================================================================== \r\n\r\n\r\n$copy-font: 'Open Sans', Helvetica, Arial, sans-serif;\r\n$headline-font: 'Exo', sans-serif;\r\n\r\n$bread-text-sm: 14px;\r\n$bread-text-md: 15px;\r\n$bread-text-lg: 16px;\r\n\r\n$bread-text-sm-line: 18px;\r\n$bread-text-md-line: 21px;\r\n$bread-text-lg-line: 24px;\r\n\r\n$tiny-text-sm: 10px;\r\n$tiny-text-md: 11px;\r\n$tiny-text-lg: 12px;\r\n\r\n$tiny-text-sm-line: 14px;\r\n$tiny-text-md-line: 16px;\r\n$tiny-text-lg-line: 18px;\r\n\r\n$small-text-sm: 11px;\r\n$small-text-md: 12px;\r\n$small-text-lg: 13px;\r\n\r\n$small-text-sm-line: 16px;\r\n$small-text-md-line: 17px;\r\n$small-text-lg-line: 18px;\r\n\r\n$large-text-sm: 16px;\r\n$large-text-md: 18px;\r\n$large-text-lg: 20px;\r\n\r\n$huge-text-sm: 26px;\r\n$huge-text-md: 36px;\r\n$huge-text-lg: 54px;\r\n\r\n$h1 : calc(2.65rem + 1vw);\r\n$h1-h2 : calc(1.65rem + 1.1vw);\r\n$h2 : calc(1.8rem + 1vw);\r\n$h3 : calc(1.4rem + 0.8vw);\r\n$h4 : calc(1.2rem + 0.4vw);\r\n$h5 : calc(0.9rem + 0.1vw);\r\n$h6 : calc(0.7rem + 0.08vw);\r\n\r\n\r\n$big-text : calc(2.5rem + 0.7vw);\r\n$bigger-text : calc(2.9rem + 0.9vw);\r\n$biggest-text : calc(3.2rem + 1.2vw);\r\n\r\n$h4-lineheight : calc(1.7rem + 0.2vw);\r\n$h4-padding : calc(1.7rem + 0.2vw) 0;\r\n\r\n$tiny-icon : calc(1.8rem + 0.4vw);\r\n$small-icon : calc(2rem + 0.5vw);\r\n$medium-icon : calc(2.6rem + 0.8vw);\r\n$big-icon : calc(4rem + 1vw);\r\n$huge-icon : calc(6rem + 1.6vw);\r\n$mega-icon: calc(8rem + 2vw);\r\n\r\n\r\n$mega-font : calc(2rem + 2vw);\r\n\r\n$lg-button: calc(4rem + 2vw);\r\n$md-button: calc(2rem + 1vw);\r\n$sm-button: calc(1rem + 0.5vw);\r\n\r\n// 3. Colors\r\n// ==========================================================================\r\n\r\n$primary-color : color(\"essblue\", \"base\");\r\n$primary-color-hover : color(\"essblue\", \"darken-3\");\r\n$primary-text : color(\"gray\", \"darken-2\");\r\n$secondary-color : color(\"gray\", \"darken-2\");\r\n$white : color(\"shades\", \"white\");\r\n\r\n//SYSTEM\r\n$success-color : color(\"green\", \"base\");\r\n$error-color : color(\"red\", \"base\");\r\n\r\n$link-color : color(\"gray\", \"base\");\r\n$ess-base-green : color(\"green\", \"base\");\r\n\r\n$card-link-color : color(\"essblue\", \"base\");\r\n$footer-font-color : color(\"gray\", \"base\");\r\n$footer-bg-color : color(\"gray\", \"lighten-4\");\r\n$footer-copyright-font-color: color(\"gray\", \"lighten-2\");\r\n$footer-copyright-bg-color : none;\r\n$ess-blue-transp : rgba(15, 42, 86, 0.2);\r\n\r\n\r\n\r\n// GRADIENT\r\n$ess-blue-gradient : linear-gradient(to left, color('gray', 'base'), color('gray', 'lighten-1'));\r\n$ess-blue-gradient-reverse : linear-gradient(to right, $primary-color, $secondary-color);\r\n\r\n\r\n// button colors \r\n$primary-button-bg : $primary-color;\r\n$primary-button-bg-hover : color('essblue', 'darken-1');\r\n\r\n\r\n\r\n// 4. Spacings\r\n// ==========================================================================\r\n\r\n\r\n$spaces-map: (\r\n  \"zero\" : 0,\r\n  \"quarter\": $quarter-margin,\r\n  \"half\": $half-margin,\r\n  \"base\" : $base-margin,\r\n  \"double\" : $double-margin,\r\n  \"quad\" : $quad-margin,\r\n  \"sexta\": $sexta-margin,\r\n  \"okta\" : $okto-margin,\r\n  \"dodeca\" : $dodeca-margin,\r\n  \"minus-base\": $-base-margin,\r\n  \"minus-double\": $-double-margin,\r\n  \"minus-quad\": $-quad-margin\r\n);\r\n\r\n$spaces-values: (\r\n  \"m\": (\"margin\"),\r\n  \"m-top\": (\"margin-top\"),\r\n  \"m-right\": (\"margin-right\"),\r\n  \"m-bot\": (\"margin-bottom\"),\r\n  \"m-left\": (\"margin-left\"),\r\n  \"m-hor\": (\"margin-left\", \"margin-right\"),\r\n  \"m-vert\": (\"margin-top\", \"margin-bottom\"),\r\n  \"p\":(\"padding\"),\r\n  \"p-top\": (\"padding-top\"),\r\n  \"p-right\": (\"padding-right\"),\r\n  \"p-bot\": (\"padding-bottom\"),\r\n  \"p-left\": (\"padding-left\"),\r\n  \"p-hor\": (\"padding-left\", \"padding-right\"),\r\n  \"p-vert\": (\"padding-top\", \"padding-bottom\"),\r\n);\r\n\r\n\r\n// 5. Top Navbar\r\n// ==========================================================================\r\n\r\n$topnav_padding: $base-margin;\r\n\r\n$nav_cube_side_width: 1.4rem;\r\n$nav_cube_side_shift: 0.9rem;\r\n$nav_cube_side_shift_2: 1rem;"],"sourceRoot":""}]);
